<template>
  <div>
    <div class="form">
      <el-form ref="form" label-width="120px" style="margin:10px;width:auto;">
        <H2>{{page_content.page_name}}</H2>
        <el-upload
          id="upload"
          ref="upload"
          action="https://jsonplaceholder.typicode.com/posts/"
          multiple
        >
          <!-- :on-preview="handlePreview"
          :on-success="insertImage"
          :on-remove="handleRemove"
          :before-remove="beforeRemove"
          :on-exceed="handleExceed"
          :file-list="fileList"-->
          <!-- <el-button ref="imageBtn" size="small" type="primary"></el-button> -->
        </el-upload>
        <!--
    editorkeytinymce keep-alivetinymce
    activatedkey
        -->
        <editor
          id="tinymceEditor"
          :init="tinymceInit"
          v-model="page_content.html_context"
          :key="tinymceFlag"
        ></editor>

        <el-form-item class="text_right">
          <el-button type="primary" @click="onSubmit()"></el-button>
          <el-button @click="onCancel()"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
import tinymce from "tinymce/tinymce";
import "tinymce/themes/silver/theme";
import Editor from "@tinymce/tinymce-vue";

import "tinymce/plugins/textcolor";
import "tinymce/plugins/advlist";
import "tinymce/plugins/table";
import "tinymce/plugins/lists";
import "tinymce/plugins/paste";
import "tinymce/plugins/preview";
import "tinymce/plugins/fullscreen";

export default {
  name: "TinymceEditor",
  components: {
    editor: Editor
  },
  data() {
    return {
      tinymceFlag: 1,
      id: "",
      tinymceInit: {},
      page_content: {
        html_context: "",
        id: "",
        page_group_id: "",
        page_name: ""
      },
      fileList: []
    };
  },
  methods: {
    // 
    insertImage(res, file) {
      let src = ""; // 
      tinymce.execCommand("mceInsertContent", false, `<img src=${src}>`);
    },
    getPageContent() {
      // id
      const url = location.href;
      if (url.indexOf("/navpageedit/edit/") !== -1) {
        //
        const ary1 = url.split("/navpageedit/edit/");
        this.id = ary1[ary1.length - 1];
      }
      //
      this.$axios
        .post(
          `https://sniweb.shouting.feedia.co/php/GetContent.php?page_id=${
            this.id
          }`
        )
        .then(res => {
          this.page_content = res.data;
          console.log(res.data);
        })
        .catch(err => console.log(err));
    },
    onSubmit() {
      //
      this.$axios
        .post(
          `https://sniweb.shouting.feedia.co/php/EditContent.php?page_id=${
            this.id
          }&sid=${window.$cookies.isKey("sid")}`,
          JSON.stringify(this.page_content)
        )
        .then(res => {
          //
          this.$message({
            message: "",
            type: "success"
          });
          this.$emit("update");
          this.$router.push("/navpageedit");
        });
    },
    onCancel() {
      this.$router.push("/navpageedit");
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        ` 3  ${
          files.length
        }  ${files.length + fileList.length} `
      );
    },
    beforeRemove(file, fileList) {
      return this.$confirm(` ${file.name}`);
    },
    test() {
      let imageBtn = this.$refs.imageBtn;
      console.log(imageBtn);
    }
  },
  created() {
    this.tinymceInit = {
      skin_url: "/tinymce/skins/ui/oxide",
      language_url: `/tinymce/langs/zh_CN.js`,
      language: "zh_CN",
      height: document.body.offsetHeight - 300,
      browser_spellcheck: true, // 
      branding: false, // 
      // elementpath: false,  //
      statusbar: false, // 
      paste_data_images: true, // 
      menubar: false, // menu
      plugins: "advlist table lists paste preview fullscreen",
      toolbar:
        "fontselect fontsizeselect forecolor backcolor bold italic underline strikethrough | alignleft aligncenter alignright alignjustify | imageUpload quicklink h2 h3 blockquote table numlist bullist preview fullscreen",
      file_browser_callback: function(field_name, url, type, win) {
        win.document.getElementById(field_name).value = "my browser value";
      },
      file_browser_callback_types: "file image media",
      /**
       * tinymce
       * iviewUpload,
       */
      setup: editor => {
        editor.ui.registry.addButton("imageUpload", {
          tooltip: "",
          icon: "image",
          onAction: () => {
            let upload = this.$refs.upload;
            console.log(upload);
            upload.handleClick();
          }
        });
      }
    };
    this.getPageContent();
  },
  activated() {
    this.tinymceFlag++;
  },
  mounted() {}
};
</script>

<style scoped>
.text_right {
  float: right;
  margin-top: 20px;
}
</style>
